// Foundation1D.js

import React, { useState } from 'react';
import './Foundation1D.css'; // Importa los estilos
import APIconfig from '../../config'; // Importamos la configuración

import sue1 from './img/sue1.png'; // Importa la imagen
import img1 from './img/img1.png'; // Importa la imagen

const API_URL = APIconfig.API_URL_FOUNDATION1D;

function Foundation1D() {
  const [formData, setFormData] = useState({
    B: 2000,
    L: 3000,
    b: 600,
    l: 800,
    H: 500,
    h: 800,
    Pu: 7,
    Vu: 5,
    Mu: 4,
    hs: 600,
    phi: 32,
    sigma_adm: 2.0,
    gamma_s: 1.7,
    gamma_h: 2.5,
  });

  const [resultados, setResultados] = useState({
    FSD: '',
    FSV: '',
    comp: '',
    tmax: '',
    status: '',
  });

  // Función para manejar los cambios en los inputs
  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setFormData((prev) => ({ ...prev, [name]: parseFloat(value) }));
  };

  // Función para validar todos los inputs antes de enviar
  const validateAllInputs = () => {
    let isValid = true;
    let errorMessage = '';

    const {
      B,
      L,
      b,
      l,
      H,
      h,
      Pu,
      Vu,
      Mu,
      hs,
      phi,
      sigma_adm,
      gamma_s,
      gamma_h,
    } = formData;

    if (isNaN(B) || isNaN(L) || isNaN(b) || isNaN(l) || isNaN(H) || isNaN(h)) {
      isValid = false;
      errorMessage =
        'Por favor, complete todos los campos de dimensiones con valores numéricos.';
    } else if (B <= 0 || L <= 0 || b <= 0 || l <= 0 || H <= 0 || h <= 0) {
      isValid = false;
      errorMessage = 'Los valores de dimensiones deben ser mayores que cero.';
    }

    if (isNaN(Pu) || isNaN(Vu) || isNaN(Mu)) {
      isValid = false;
      errorMessage =
        'Por favor, complete todos los campos de cargas con valores numéricos.';
    }

    if (
      isNaN(hs) ||
      isNaN(phi) ||
      isNaN(sigma_adm) ||
      isNaN(gamma_s) ||
      isNaN(gamma_h)
    ) {
      isValid = false;
      errorMessage =
        'Por favor, complete todos los campos de suelo con valores numéricos.';
    }

    if (!isValid) {
      alert(errorMessage); // Mostrar mensaje de error
    }

    return isValid;
  };

  // Función para manejar el envío del formulario
  const handleSubmit = async (event) => {
    event.preventDefault();

    if (!validateAllInputs()) {
      return;
    }

    try {
      const response = await fetch(API_URL, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      const responseData = await response.json();

      // Actualiza el estado con los resultados
      setResultados(responseData);
    } catch (error) {
      console.error('Error:', error);
      alert('Ocurrió un error al comunicarse con el servidor.');
    }
  };

  return (
    <div className="containerF1D-general">
      <div className="containerF1D-1">
        <div className="title-sectionF1D">
          <i>
            <h1>Verificación de Fundaciones 1D</h1>
          </i>
        </div>
        <div className="form-sectionF1D">
          <form onSubmit={handleSubmit}>
            <div className="form-groupF1D-1">
              <div className="dimensionsF1D">
                <i>
                  <h2>Dimensiones</h2>
                </i>
                <div className="fields1-F1D">
                  <div className="fields1-1-F1D">
                    <h3>Zapata:</h3>
                    <label htmlFor="B">Ancho zapata, B [mm]:</label>
                    <input
                      type="number"
                      id="B"
                      name="B"
                      step="1"
                      value={formData.B}
                      onChange={handleInputChange}
                      required
                    />
                    <label htmlFor="L">Largo zapata, L [mm]:</label>
                    <input
                      type="number"
                      id="L"
                      name="L"
                      step="1"
                      value={formData.L}
                      onChange={handleInputChange}
                      required
                    />
                    <label htmlFor="H">Altura zapata, H [mm]:</label>
                    <input
                      type="number"
                      id="H"
                      name="H"
                      step="1"
                      value={formData.H}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                  <div className="fields1-2-F1D">
                    <h3>Columna:</h3>
                    <label htmlFor="b">Ancho columna, b [mm]:</label>
                    <input
                      type="number"
                      id="b"
                      name="b"
                      step="1"
                      value={formData.b}
                      onChange={handleInputChange}
                      required
                    />
                    <label htmlFor="l">Largo columna, l [mm]:</label>
                    <input
                      type="number"
                      id="l"
                      name="l"
                      step="1"
                      value={formData.l}
                      onChange={handleInputChange}
                      required
                    />
                    <label htmlFor="h">Altura columna, h [mm]:</label>
                    <input
                      type="number"
                      id="h"
                      name="h"
                      step="1"
                      value={formData.h}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                </div>
              </div>
              <div className="cargasF1D">
                <i>
                  <h2>Cargas</h2>
                </i>
                <div className="fields2-F1D">
                  <label htmlFor="Pu">Carga axial, Pu [tonf]:</label>
                  <input
                    type="number"
                    id="Pu"
                    name="Pu"
                    step="1"
                    value={formData.Pu}
                    onChange={handleInputChange}
                    required
                  />
                  <label htmlFor="Vu">
                    Esfuerzo de corte último, Vu [tonf]:
                  </label>
                  <input
                    type="number"
                    id="Vu"
                    name="Vu"
                    step="1"
                    value={formData.Vu}
                    onChange={handleInputChange}
                    required
                  />
                  <label htmlFor="Mu">Momento, Mu [tonf-m]:</label>
                  <input
                    type="number"
                    id="Mu"
                    name="Mu"
                    step="1"
                    value={formData.Mu}
                    onChange={handleInputChange}
                    required
                  />
                </div>
              </div>
            </div>

            <div className="form-groupF1D-2">
              <div className="parametrosF1D">
                <i>
                  <h2>Parámetros de suelo</h2>
                </i>
                <div className="fields3-F1D">
                  <label htmlFor="hs">
                    Altura suelo de relleno, h<sub>S</sub> [mm]:
                  </label>
                  <input
                    type="number"
                    id="hs"
                    name="hs"
                    step="1"
                    value={formData.hs}
                    onChange={handleInputChange}
                    required
                  />
                  <label htmlFor="phi">
                    Ángulo de fricción interna, &phi; [°]:
                  </label>
                  <input
                    type="number"
                    id="phi"
                    name="phi"
                    step="1"
                    value={formData.phi}
                    onChange={handleInputChange}
                    required
                  />
                  <label htmlFor="sigma_adm">
                    Tensión admisible del suelo, &sigma;<sub>adm</sub> [kgf/cm
                    <sup>2</sup>]:
                  </label>
                  <input
                    type="number"
                    id="sigma_adm"
                    name="sigma_adm"
                    step="1"
                    value={formData.sigma_adm}
                    onChange={handleInputChange}
                    required
                  />
                  <label htmlFor="gamma_s">
                    Peso unitario relleno, &gamma;<sub>s</sub> [tonf/m
                    <sup>3</sup>]:
                  </label>
                  <input
                    type="number"
                    id="gamma_s"
                    name="gamma_s"
                    step="1"
                    value={formData.gamma_s}
                    onChange={handleInputChange}
                    required
                  />
                  <label htmlFor="gamma_h">
                    Peso unitario hormigón, &gamma;<sub>h</sub> [tonf/m
                    <sup>3</sup>]:
                  </label>
                  <input
                    type="number"
                    id="gamma_h"
                    name="gamma_h"
                    step="1"
                    value={formData.gamma_h}
                    onChange={handleInputChange}
                    required
                  />
                </div>
              </div>
              <div className="imageContainer-F1D">
                <img
                  id="img1"
                  src={img1}
                  alt="dimensiones y cargas"
                  width="70%"
                />
                <img id="sue1" src={sue1} alt="suelo" width="30%" />
              </div>
            </div>
            <button type="submit">
              <i>
                <b>Calcular</b>
              </i>
            </button>
          </form>
        </div>
      </div>
      <div className="containerF1D-2">
        <i>
          <h2>Resultados</h2>
        </i>
        <div className="resultsF1D">
          <p>
            FS<sub>D</sub>: <span id="FSD">{resultados.FSD}</span>
          </p>
          <p>
            FS<sub>V</sub>: <span id="FSV">{resultados.FSV}</span>
          </p>
          <p>
            Área a compresión en la zapata [%]:{' '}
            <span id="comp">{resultados.comp}</span>
          </p>
          <p>
            Tensión máxima de compresión [tonf/m<sup>2</sup>]:{' '}
            <span id="tmax">{resultados.tmax}</span>
          </p>
          <p>
            Status: <span id="status">{resultados.status}</span>
          </p>
        </div>
      </div>
    </div>
  );
}

export default Foundation1D;
