// src/components/library/library.js
import React, { useState } from 'react';
import './Library.css';
import APIconfig from '../../config'; // Importamos la configuración

const API_URL = APIconfig.API_URL_LIBRARY;

function Library() {
  const [inputs, setInputs] = useState([
    { archivo: null, tipo: '' },
    { archivo: null, tipo: '' },
    { archivo: null, tipo: '' },
    { archivo: null, tipo: '' },
    { archivo: null, tipo: '' },
    { archivo: null, tipo: '' },
  ]);
  const [loading, setLoading] = useState(false);

  const handleArchivoChange = (event, index) => {
    const nuevosInputs = [...inputs];
    nuevosInputs[index].archivo = event.target.files[0] || null;
    setInputs(nuevosInputs);
  };

  const handleTipoChange = (event, index) => {
    const nuevosInputs = [...inputs];
    nuevosInputs[index].tipo = event.target.value;
    setInputs(nuevosInputs);
  };

  const handleSubmit = async () => {
    const files = [];
    const tipos = [];

    // Filtramos para sacar los vacíos
    inputs.forEach((item) => {
      if (item.archivo && item.tipo) {
        files.push(item.archivo);
        tipos.push(item.tipo);
      }
    });

    if (files.length === 0) {
      alert('No se seleccionó ningún archivo.');
      return;
    }

    const formData = new FormData();

    // Agregamos los archivos al FormData
    files.forEach((archivo) => {
      formData.append('files', archivo);
    });

    // Agregamos los tipos al FormData, serializados en formato JSON
    formData.append('tipos', JSON.stringify(tipos));

    // Log
    console.log('Archivos a enviar:', files);
    console.log('Tipos:', tipos);

    setLoading(true);

    try {
      const response = await fetch(API_URL, {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(errorText);
      }

      const blob = await response.blob();
      const url = window.URL.createObjectURL(blob);

      const a = document.createElement('a');
      a.href = url;
      a.download = 'archivos_combinados.zip';
      document.body.appendChild(a);
      a.click();
      a.remove();

      window.URL.revokeObjectURL(url);
    } catch (error) {
      console.error('Error: ' + error.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="library-LB">
      <div className="title-sectionLB">
        <h1>Auto MTO</h1>
      </div>
      <p>Seleccione sus archivos .xls de MTO:</p>
      <div className="uploader-LB">
        {inputs.map((item, index) => (
          <div
            className="inputs-LB"
            key={index}
            style={{ marginBottom: '10px' }}
          >
            <input
              type="file"
              onChange={(e) => handleArchivoChange(e, index)}
            />
            <div className="selectors-LB">
              <select
                value={item.tipo}
                onChange={(e) => handleTipoChange(e, index)}
              >
                <option value=""> Seleccione tipo </option>
                <option value="aceros">Aceros</option>
                <option value="hormigones">Hormigones</option>
                <option value="grating">Grating</option>
                <option value="escaleras">Escaleras</option>
                <option value="barandas">Barandas</option>
                <option value="manual">Manual</option>
              </select>
            </div>
          </div>
        ))}
      </div>
      <div className="buttonContainer-LB">
        <button onClick={handleSubmit} disabled={loading}>
          {loading ? 'Generando MTO...' : 'Generar MTO'}
        </button>
      </div>
      {loading && (
        <div className="spinner-container-LB">
          <div className="spinner-LB"></div>
          <p>Cargando...</p>
        </div>
      )}
    </div>
  );
}

export default Library;
