import React, { useState, useEffect, useRef } from 'react';
import Chart from 'chart.js/auto'; // Importa Chart.js
import './SpectrePlot.css'; // Importa los estilos (si los tienes)
import APIconfig from '../../config'; // Importamos la configuración

const API_URL = APIconfig.API_URL_SPECTREPLOT;

function SpectrePlot() {
  const [selectedNorm, setSelectedNorm] = useState('');
  const [formData, setFormData] = useState({
    ZS: '',
    Tsuelo: '',
    cat: '',
    R: '',
    xi: '',
  });
  const [chartData, setChartData] = useState(null);
  const chartRef = useRef(null); // Referencia al elemento canvas
  const chartInstance = useRef(null); // Referencia a la instancia del gráfico

  // Define las opciones de los campos del formulario según la norma
  const normOptions = {
    2003: [
      {
        label: 'Zona Sísmica',
        type: 'select',
        id: 'ZS',
        options: ['1', '2', '3'],
      },
      {
        label: 'Tipo de suelo',
        type: 'select',
        id: 'Tsuelo',
        options: ['I', 'II', 'III'],
      },
      {
        label: 'Categoría de ocupación del edificio',
        type: 'select',
        id: 'cat',
        options: ['C1', 'C2', 'C3'],
      },
      {
        label: 'Factor de modificación de la respuesta (R)',
        type: 'select',
        id: 'R',
        options: ['1', '2', '3', '4', '5'],
      },
      {
        label: 'Razón de amortiguamiento (ξ)',
        type: 'select',
        id: 'xi',
        options: ['0.02', '0.03', '0.05'],
      },
    ],
    2023: [
      {
        label: 'Zona Sísmica',
        type: 'select',
        id: 'ZS',
        options: ['1', '2', '3'],
      },
      {
        label: 'Tipo de suelo',
        type: 'select',
        id: 'Tsuelo',
        options: ['A', 'B', 'C', 'D'],
      },
      {
        label: 'Categoría de ocupación del edificio',
        type: 'select',
        id: 'cat',
        options: ['I', 'II', 'III', 'IV'],
      },
      {
        label: 'Factor de modificación de la respuesta (R)',
        type: 'select',
        id: 'R',
        options: [
          '1.0',
          '1.5',
          '2.0',
          '2.5',
          '3.0',
          '3.5',
          '4.0',
          '4.5',
          '5.0',
        ],
      },
      {
        label: 'Razón de amortiguamiento (ξ)',
        type: 'select',
        id: 'xi',
        options: ['0.02', '0.03', '0.05'],
      },
    ],
  };

  // // Función para bajar resolución de los datos entregados (para compatibilidad con software)
  // const lttbDownsample = (data, threshold) => {
  //   if (!data || threshold <= 0) return {};

  //   const xValues = Object.keys(data)
  //     .map(parseFloat)
  //     .sort((a, b) => a - b);
  //   const yValues = xValues.map((x) => data[x]); // Obtener los valores de Y en el orden de X

  //   const dataLength = xValues.length;
  //   if (threshold >= dataLength || dataLength === 0) {
  //     const result = {};
  //     xValues.forEach((x) => (result[x] = data[x]));
  //     return result;
  //   }

  //   const sampled = {};
  //   const bucketSize = (dataLength - 2) / (threshold - 2);

  //   let a = 0;
  //   sampled[xValues[0]] = data[xValues[0]]; // Siempre incluir el primer punto

  //   for (let i = 0; i < threshold - 2; i++) {
  //     let avgX = 0;
  //     let avgY = 0;
  //     let avgRangeStart = Math.floor((i + 1) * bucketSize) + 1;
  //     let avgRangeEnd = Math.floor((i + 2) * bucketSize) + 1;
  //     avgRangeEnd = Math.min(avgRangeEnd, dataLength);

  //     let avgRangeLength = avgRangeEnd - avgRangeStart;

  //     for (; avgRangeStart < avgRangeEnd; avgRangeStart++) {
  //       avgX += xValues[avgRangeStart];
  //       avgY += yValues[avgRangeStart];
  //     }
  //     avgX /= avgRangeLength;
  //     avgY /= avgRangeLength;

  //     let pointAX = xValues[a];
  //     let pointAY = yValues[a];

  //     let maxArea = -1;
  //     let nextA = 0;

  //     for (
  //       let rangeOffs = Math.floor((i + 1) * bucketSize) + 1;
  //       rangeOffs < avgRangeEnd;
  //       rangeOffs++
  //     ) {
  //       let pointCX = xValues[rangeOffs];
  //       let pointCY = yValues[rangeOffs];

  //       let area =
  //         Math.abs(
  //           (pointAX - avgX) * (pointCY - pointAY) -
  //             (pointAX - pointCX) * (avgY - pointAY)
  //         ) * 0.5; //Area del triangulo
  //       if (area > maxArea) {
  //         maxArea = area;
  //         nextA = rangeOffs;
  //       }
  //     }

  //     sampled[xValues[nextA]] = data[xValues[nextA]];
  //     a = nextA;
  //   }

  //   sampled[xValues[dataLength - 1]] = data[xValues[dataLength - 1]]; // Siempre incluir el último punto

  //   return sampled;
  // };

  //Función para descargar datos como txt

  const downloadDataAsCsv = (data) => {
    // Verifica si hay datos o si el objeto está vacío
    if (!data || Object.keys(data).length === 0) {
      console.warn('No data provided or data is empty.');
      return;
    }

    const xValues = Object.keys(data);
    let content = ''; // Inicializa el contenido vacío

    // Define las opciones de formato para toLocaleString
    // - locale 'es-ES': Usa ',' como separador decimal. Puedes usar otros como 'de-DE', 'fr-FR', 'it-IT'.
    // - useGrouping: false: Evita el separador de millares (ej: 1.000 -> 1000).
    const formatOptions = {
      useGrouping: false,
      // Opcional: Puedes definir un número máximo/mínimo de decimales si lo necesitas.
      // Ejemplo: Para mostrar siempre al menos 2 decimales y máximo 6:
      minimumFractionDigits: 3,
      maximumFractionDigits: 3,
    };
    const locale = 'en-US'; // Elige el locale que prefieras que use coma decimal

    for (let i = 0; i < xValues.length; i++) {
      const xValueKey = xValues[i]; // La clave original (string)
      const yValueRaw = data[xValueKey]; // El valor original

      // Intenta convertir las claves y valores a números flotantes
      const x = parseFloat(xValueKey);
      const y = parseFloat(yValueRaw);

      // Verifica que AMBOS sean números válidos después de la conversión
      // isNaN() es la forma más segura de verificar si parseFloat falló
      if (!isNaN(x) && !isNaN(y)) {
        // Formatea los números usando toLocaleString con las opciones definidas
        const formattedX = x.toLocaleString(locale, formatOptions);
        const formattedY = y.toLocaleString(locale, formatOptions);

        // Añade la línea formateada al contenido del archivo
        content += `${formattedX}; ${formattedY}\n`;
      } else {
        // Log de diagnóstico mejorado si algún valor no es un número válido
        console.warn(
          `Skipping entry: Invalid number detected. X Key: "${xValueKey}" (parsed as ${x}), Y Value: "${yValueRaw}" (parsed as ${y})`
        );
      }
    }

    // Si después del bucle no hay contenido válido, no descarga
    if (!content) {
      console.warn('No valid data points found to download.');
      return;
    }

    // --- Lógica de descarga (sin cambios, pero con mejora en Blob type) ---
    // Especificar charset=utf-8 es buena práctica para compatibilidad
    const blob = new Blob([content], { type: 'text/plain;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'chart_data.csv'; // Nombre del archivo
    document.body.appendChild(a); // Añadir al DOM para que funcione en Firefox
    a.click(); // Simular clic para iniciar la descarga
    document.body.removeChild(a); // Limpiar el elemento del DOM
    URL.revokeObjectURL(url); // Liberar la memoria del objeto URL
  };

  // Función para manejar el cambio de la norma seleccionada
  const handleNormChange = (event) => {
    setSelectedNorm(event.target.value);
    setFormData({
      // Resetear el formulario al cambiar la norma
      ZS: '',
      Tsuelo: '',
      cat: '',
      R: '',
      xi: '',
    });
    setChartData(null); // Limpiar los datos del gráfico al cambiar la norma
  };

  // Función para manejar los cambios en los campos del formulario
  const handleInputChange = (event) => {
    setFormData({
      ...formData,
      [event.target.name]: event.target.value,
    });
  };

  // Función para manejar el envío del formulario
  const handleSubmit = async (event) => {
    event.preventDefault();

    const data = {
      function_type: selectedNorm,
      ZS: parseFloat(formData.ZS),
      Tsuelo: formData.Tsuelo,
      R: parseFloat(formData.R),
      xi: parseFloat(formData.xi),
      cat: formData.cat,
    };
    console.log('data enviada: ', data);

    try {
      const response = await fetch(API_URL, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      const responseData = await response.json();
      setChartData(responseData); // Actualiza el estado con los datos del gráfico
      //createChart(responseData); // Crea o actualiza el gráfico
    } catch (error) {
      console.error('Error al obtener los datos:', error);
    }
  };

  // Effect para crear o actualizar el gráfico cuando cambian los datos
  useEffect(() => {
    if (chartData) {
      const chartDataFormatted = {
        labels: Object.keys(chartData),
        datasets: [
          {
            label: 'Espectro de Diseño',
            data: Object.values(chartData),
            backgroundColor: '#003C71',
            borderColor: '#003C71',
            borderWidth: 3,
            pointStyle: false,
          },
        ],
      };
      const chartOptions = {
        responsive: true,
        plugins: {
          customCanvasBackgroundColor: {
            color: 'lightGreen',
          },
        },
        scales: {
          y: {
            beginAtZero: true,
            suggestedMax: Math.max(...Object.values(chartData)) + 0.05,
            title: {
              display: true,
              text: 'Sa [mm/s2]',
            },
            grid: {
              color: 'rgba(200, 200, 200, 0.8)',
              lineWidth: 1,
              drawBorder: true,
              drawOnChartArea: true,
              drawTicks: true,
            },
          },
          x: {
            type: 'linear',
            title: {
              display: true,
              text: 'Período, T [s]',
            },
            grid: {
              color: 'rgba(200, 200, 200, 0.8)',
              lineWidth: 1,
              drawBorder: true,
              drawOnChartArea: true,
              drawTicks: true,
            },
          },
        },
      };

      const ctx = chartRef.current.getContext('2d');
      if (chartInstance.current) {
        chartInstance.current.destroy();
      }
      chartInstance.current = new Chart(ctx, {
        type: 'line',
        data: chartDataFormatted,
        options: chartOptions,
      });
    }
  }, [chartData]);

  return (
    <div className="containerSP">
      <div className="title-sectionSP">
        <i>
          <h1>Espectro de diseño NCh2369</h1>
        </i>
      </div>
      <div className="form-sectionSP">
        <div className="norm-selectorSP">
          <h2>
            <i>Seleccionar Norma</i>
          </h2>
          <select
            id="norm-select"
            value={selectedNorm}
            onChange={handleNormChange}
          >
            <option value="" disabled>
              Seleccione una norma
            </option>
            <option value="2003">Norma 2003</option>
            <option value="2023">Norma 2023</option>
          </select>
        </div>
        {selectedNorm && (
          <div className="form-containerSP">
            <i>
              <h2>Parámetros</h2>
            </i>
            <form id="spectreForm" onSubmit={handleSubmit}>
              <div className="form-fields-containerSP">
                {normOptions[selectedNorm].map((field) => (
                  <div className="form-groupSP" key={field.id}>
                    <label htmlFor={field.id}>{field.label}:</label>
                    {field.type === 'select' ? (
                      <select
                        id={field.id}
                        name={field.id}
                        value={formData[field.id]}
                        onChange={handleInputChange}
                        required
                      >
                        <option value="" disabled>
                          Seleccionar
                        </option>
                        {field.options.map((option) => (
                          <option key={option} value={option}>
                            {option}
                          </option>
                        ))}
                      </select>
                    ) : null}
                  </div>
                ))}
              </div>
              <button type="submit">Calcular</button>
            </form>
          </div>
        )}
      </div>

      {chartData && (
        <div className="results-sectionSP">
          <i>
            <h2>Resultados</h2>
          </i>
          <div className="results-subcontainerSP">
            <div className="graph-containerSP">
              <canvas id="myChart" ref={chartRef}></canvas>
            </div>
            <div className="buttons-containerSP">
              <button
                id="downloadCSVButtonSP"
                onClick={() => downloadDataAsCsv(chartData)}
              >
                Descargar archivo CSV
              </button>
              <button id="downloadRISAButtonSP" onClick={() => null}>
                Descargar CSV (RISA)
              </button>
              <button id="downloadSAPButtonSP" onClick={() => null}>
                Descargar CSV (SAP)
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default SpectrePlot;
